@import '../variables.scss';

.header{
    background: $darkBrown;
    width: 100%;
    height: $headerHeight;
    padding: 0 1.5rem;
    @include breakpoint(md){
        padding: 0 3rem;
    }
    transition: 0.3s ease all;
    &_content{
        overflow: hidden;
        color: $bg;
        margin: 0 auto;
        max-width: 1920px;
        height: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: relative;
        z-index: 100;
        &_logo{
            font-size: 2rem;
            font-weight: bold;
            text-decoration: none;
            color: $light;
            display: flex;
            flex-direction: row;
            align-items: center;
           a{
               text-decoration: none;
               color: inherit;
               padding: 0.75rem 1.5rem;
               border-radius: $borderRadius;
               transition: 0.3s ease all;
               text-transform: capitalize;
               &:hover{
                   background: rgba($light,0.1);
               }
           }
        }
        &_nav{
            top: 0;
            right: 100%;
            bottom: 0;
            width: 100%;
            height: 100vh;
            position: fixed;
            display: flex;
            flex-direction: column;
            justify-content: center;
            text-align: center;
            background: rgba($dark,0.9);
            backdrop-filter: blur(2px);
            transform: translate(0);
            transition: 0.3s ease all;
            @include breakpoint(md){
                transform: none;
                flex-direction: row;
                background-color: transparent;
                width: auto;
                height: 100%;
                position: static;
            }
            ul{
                list-style: none;
                padding: 0;
                display: flex;
                flex-direction: column;
                margin-bottom: $spacing-lg;
                @include breakpoint(md){
                    flex-direction: row;
                    align-items: center;
                    margin-bottom: 0;
                    margin-right: calc(0.5rem + #{$spacing-md});
                }
                li{
                    &:not(:last-child){
                        margin-bottom: $spacing-lg;
                        @include breakpoint(md) {
                            margin-bottom: 0;
                            margin-right: $spacing-md;
                        } 
                    }
                    a{
                        text-decoration: none;
                        color: inherit;
                        padding: 0.75rem 1.25rem;
                        border-radius: $borderRadius;
                        transition: 0.3s ease all;  
                        &:hover{
                            color: $primary;
                        }  
                    }
                }
            }
             &.isMenu {
                    transform: translate(100%);
            }
        }
        &_toggle{
            cursor: pointer;
            display: flex;
            align-items: center;
            font-size: 2rem;
            transition:0.3s ease all;
            position: relative;
            &:hover{
                color: $primary;
            }
            @include breakpoint(md){
                display: none;
            }
        }
    }
}